<template>
  <v-card-text>
    <div class="d-flex">
      <h1 style="margin-left: 46%;">Services </h1>
      <v-spacer></v-spacer>
    </div>
    <br />
    <div style="text-align: start">
      <v-row>
        <v-col xl=3 lg=3 sm=12 md=6 xs=24 v-for="service in services" :key="service._id">
          <v-card @click.native="clickModbusDialog(service.service)" elevation="2" outlined tile class="card-height">
            <v-card-title>
              <h5>{{ service.label }}</h5>
              <v-row align="center" justify="end" >
                <v-avatar size="35">
                  <v-icon>mdi-{{service.icon}}</v-icon>
                </v-avatar>
              </v-row>
            </v-card-title>
            <v-card-subtitle v-if="service.status">{{service.status.status ? "Running" : "Not Running"}}&nbsp;</v-card-subtitle>
            <v-card-text>
              <span v-if="service.status" class="d-flex">start at:&nbsp;
                <span class="value-style text-truncate">{{service.status.time_since}},&nbsp;{{service.status.date_since | formatDate}}</span>&nbsp;
              </span>
            </v-card-text>
            <v-card-actions class="btns-row">
              <v-row>
                <v-btn @click="clickDialog('start', service.name)" x-small color="blue darken-2" class="services-btn" text><v-icon>mdi-play-circle-outline</v-icon>&nbsp;Start</v-btn>
                <v-btn @click="clickDialog('stop', service.name)" x-small color="red darken-2" class="services-btn" text><v-icon>mdi-stop-circle-outline</v-icon>&nbsp;Stop</v-btn>
              </v-row>
              &nbsp;
              <v-row>
                <v-btn @click="clickDialog('restart', service.name)" x-small color="green darken-2" class="services-btn" text><v-icon>mdi-restart</v-icon>&nbsp;Restart</v-btn>
                <v-btn @click="refresh(service.name)" x-small color="pink darken-2" class="services-btn" text><v-icon>mdi-refresh</v-icon>&nbsp;Refresh</v-btn>
              </v-row>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </div>

    <!--Confirm dialog-->
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm?
        </v-card-title>
        <v-card-text>Do you want to {{this.action}}?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="closeConfirmDialog" >
            No
          </v-btn>
          <v-btn color="red darken-1" text @click="callService(action,currentState)">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--Modbus Dialog-->
    <v-dialog v-model="modbusDialog" persistent max-width="700" overflow-y="none">
      <v-card class="modbusDialog">
        <v-card-title class="headline">
          Bac Rest Information
          <v-row align="center" justify="end" >
              <v-btn color="red darken-1" text @click="closepointServerDialog" align="end" >
                Close
              </v-btn>
          </v-row>
        </v-card-title>
        <v-card-text >
          <ul style=" list-style-type:none">
              <li v-for="(value,key) in pointServerData">
                <v-row >
                  <v-col style="text-align:start;font-variant: all-small-caps;">
                    {{key}}:
                  </v-col>
                  <v-col style="text-align:start">
                    {{value}}
                  </v-col>
                </v-row>
              </li>
            </ul>
        </v-card-text>
      </v-card>
    </v-dialog>


  </v-card-text>
</template>

<script>
import {wiresPlatPort} from "../utils/utils";
const port = wiresPlatPort();
import axios from "../axios";
import DeviceDetailForm from "./DeviceDetailForm";
import logo from '../serviceicons/nube-frog-green.png';
export default {
  components: {DeviceDetailForm},
  data: () => ({
    logo,
    confirmDialog:false,
    action:'',
    currentState:'',
    valid: false,
    pointServerData:{},
    modbusDialog:false,
    services: [],
    rules: {
      required: (value) => !!value || "Required.",
    },
  }),
  async mounted() {
    const { services } = this.$store.state;
    if (services.length > 0) {
      this.getServiceData(services);
    }
    if(Object.keys(this.pingData) !== 0){
      this.getpointServerData(this.pingData);
    }
  },
  computed: {
    setting () {
      return this.$store.state.setting;
    },
    pingData(){
      return this.$store.state.pingData;
    }
  },
  watch: {
    '$store.state.pingData':function(val){
        this.getpointServerData(val);
    },
    '$store.state.services':function(val){
      if (val.length > 0) {
        this.getServiceData(val);
      }
    },
  },
  methods: {
    closepointServerDialog(){
      this.modbusDialog=false;
    },
    getpointServerData(data){
      const arrData=Object.keys(data);
      arrData.forEach(e=>{
        if(Object.isExtensible(data[e])){
           if(Array.isArray(data[e])){
             data[e].forEach(i=>{
               Object.assign(data,i);
             })
           }
           Object.assign(data,data[e]);
        }
      })
      const newData=Object.keys(data);
      const newValueData=Object.values(data);
      for(let i = 0; i < newValueData.length; i++){
        if(!Object.isExtensible(newValueData[i])){
          const tempObj = new Object();
          tempObj[newData[i]]=newValueData[i];
          Object.assign(this.pointServerData,tempObj);
        }
      }
    },
    clickModbusDialog(service){
      if (service !== 'POINT_SERVER') {
        return;
      }
      if(Object.keys(this.pingData).length!==0){
        this.modbusDialog=true;
        return;
      }
      this.$toasted.show('Point Server is not running',{type:"error"});
    },
    closeConfirmDialog(){
      this.confirmDialog=false;
      this.action='';
      this.currentState='';
    },
    clickDialog(service,state){
      this.action=service;
      this.currentState=state;
      this.confirmDialog=true;
    },
    async getServiceData(services) {
      const result = [];
      for (const service of services) {
        if (!service.show) {
          continue;
        }
        const status = await this.getServiceStatus(service.name);
        result.push({...service, status });
      }
      this.services = result;
    },
    async getServiceStatus(service) {
      const result = await axios.get(`rubix-service/service-stats/${service}`);
      if(result){
        return result;
      }
    },
    async refresh(serviceName) {
      const index = this.services.findIndex(s=> s.name === serviceName)
      const status = await this.getServiceStatus(serviceName);
      this.services[index].status = status;
    },
    async callService(action, service) {
      this.$store.commit('changeLoading', true);
      axios.post(`rubix-service/call-service`, {action, service})
        .then(r => {
          this.$toasted.show('success');
          this.$store.commit('changeLoading', false);
          this.confirmDialog=false;
          this.action='';
          this.currentState='';
        });
    },

  },
};
</script>
<style scoped>
