<template>
  <div>
    <div class="text-left">
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn v-bind="attrs" v-on="on" icon @click="save(item)" color="success">
            <v-icon>mdi-content-save-outline</v-icon>
          </v-btn>
        </template>
        <span>Save</span>
      </v-tooltip> &nbsp;
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn @click="clickImport" v-bind="attrs" v-on="on" icon color="primary" >
            <v-icon>mdi-import</v-icon>
          </v-btn>
        </template>
        <span>Import</span>
      </v-tooltip> &nbsp;
      <v-tooltip bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-btn @click="clickJsonExport" v-bind="attrs" v-on="on" icon color="#009688">
            <v-icon>mdi-export</v-icon>
          </v-btn>
        </template>
        <span>Export</span>
      </v-tooltip>

    </div>

    <v-row>
      <v-col cols="9">
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-text-field
            @input="onChangeUrl"
            required :rules="urlRules"
            v-model="item.url"
            placeholder="Enter your request Url"
            @keyup.enter.native="send(item)"
          >

            <v-select
              class="slb-method"
              slot="prepend"
              v-model="item.method"
              :items="['Get', 'Post', 'Put', 'Patch', 'Delete']"
            ></v-select>
          </v-text-field>
        </v-form>
      </v-col>
      <v-col cols="3" class="tool-box">
        <v-btn small :disabled="!valid" @click="send(item)" color="primary">
          Send
        </v-btn> &nbsp;

      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-tabs v-model="tab">
          <v-tab>Params</v-tab>
          <v-tab>Headers</v-tab>
          <v-tab>Body</v-tab>
          <v-tab>Setting</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="'params'">
            <key-value-form :changeData="onChangeParams" :items="item.params"></key-value-form>
          </v-tab-item>
          <v-tab-item :key="'headers'">
            <key-value-form :type="'header'" :items="item.headers"></key-value-form>
          </v-tab-item>
          <v-tab-item :key="'body'">
            <key-value-form :items="item.body"></key-value-form>
          </v-tab-item>
          <v-tab-item :key="'setting'">
            <v-text-field
              v-model="item.name"
              placeholder="Name"
              label="Name"
            >
            </v-text-field>
          </v-tab-item>
        </v-tabs-items>
      </v-col>
      <v-col style="background: #000">
        <vue-json-pretty style="text-align: left" :path="'res'" :data="resp || 'Hit Send to get a response'"> </vue-json-pretty>
      </v-col>
    </v-row>
    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>

        <v-card-text>
          <v-alert
            class="text-left"
            dense colored-border
            type="error" elevation="2"
            v-show="disableSave && dataImport"
          >Incorrect Data Type!</v-alert>
          <v-container>
            <v-textarea
              outlined
              name="input-7-4"
              label="import data"
              v-model="dataImport"
              @input="onChangeImportData"
            ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

import VueJsonPretty from 'vue-json-pretty';
import 'vue-json-pretty/lib/styles.css';
import KeyValueForm from './KeyValueForm.vue';
import qs from 'query-string';
import { copyToClipboard, isJsonString } from '../utils/utils';

export default {
    props: ['item', 'save', 'remove', 'send', 'resp'],
    data: () => ({
      tab: '',
      valid: false,
      importDialog: false,
      disableSave: false,
      dataImport: "",
    }),
    components: {
      VueJsonPretty,
      KeyValueForm
    },
    computed: {
      urlRules() {
        return [
          (value) => !!value || "Required.",
        ];
      },
    },
    mounted() {
      this.$refs.form.validate();
    },
    methods: {
      async saveImport() {
          const item = JSON.parse(this.dataImport);
          await this.save(item)
          this.importDialog = false;
      },
      clickImport() {
          this.importDialog = true;
      },
      closeDialogImport(){
          this.importDialog = false;
      },
      async onChangeImportData(){
          this.disableSave = !isJsonString(this.dataImport);
      },
      clickJsonExport(){
          const status = copyToClipboard(JSON.stringify(this.item));
          const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
          const type = status ? `success` : `error`;
          return this.$toasted.show(msg, {type: type});
      },
      onChangeParams() {
        if (this.item.url) {
          this.item.url = this.item.url.split(/[?#]/)[0];
        }
        
        const params = {};
        for (const p of this.item.params) {
          if (p.key && p.value && p.enable) {
            params[p.key] = p.value;
          }
        }
        if (Object.keys(params).length > 0 && this.item.url) {
          this.item.url += '?' + qs.stringify(params);
        }
      },
      onChangeUrl() {
        const valid = this.$refs.form.validate();
        if (valid) {
          const queryString = qs.extract(this.item.url);
          if (queryString) {
            const obj = qs.parse(queryString)
            const params = [];
            for (const key in obj) {
              if (key && obj[key]) {
                params.push({key, value: obj[key], enable: true});
              }
            }
            this.item.params = params;
          }
        }
      },

    }
};
</script>
<style>
  .v-input__prepend-outer {
    margin-top: 0px;
  }
  .slb-method {
    margin-top: 0;
    padding-top: 0;
  }
  .tool-box {
    display: flex;
    align-items: center;
    text-align: left;
  }
</style>
