<template>
  <div>
    <h1>UPDATE SERVICE - {{service.name}}</h1>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step :complete="e1 > 1" step="1">Release version</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 2" step="2">Service status</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 3" step="3">Download</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 4" step="4">Install</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :complete="e1 > 5" step="5">Start</v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-radio-group v-model="selectedId">
            <v-radio
              v-for="r in releases"
              :key="r.id"
              :label="r.name"
              :value="r.id"
            ></v-radio>
          </v-radio-group>

          <v-btn color="primary" @click="e1 = 2">
            Continue
          </v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <div style="text-align: left">
            Service: {{service.name}} <br />
            Status: {{serviceStatus.status ? 'Running': 'Stopped'}} <br />
            Current Version: {{installedService ? installedService.version: 'Not Installed'}} <br />
          </div>
          <v-btn @click="e1 = 1">Back</v-btn>
          <v-btn @click="getServiceStatus" color="success">Reload</v-btn>
          <v-btn @click="stopService()" color="primary">Continue</v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <div style="text-align: left">
            Name: <a target="_blank" :href="selectedRelease.html_url">{{selectedRelease.name}}</a> <br />
            Author: <strong>{{selectedRelease.author.login}}</strong> <br />
            Created at: {{selectedRelease.created_at | formatDate}} <br />
            <a target="_blank" :href="selectedRelease.zipball_url">Source code (zip)</a> <br />
            <a target="_blank" :href="selectedRelease.tarball_url">Source code (tar.gz)</a> <br />
          </div>
          <v-btn @click="e1 = 2">
            Back
          </v-btn>
          <v-btn @click="download" color="primary">
            Download
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="4">
          <div style="text-align: left">
            Install Service
          </div>
          <v-btn @click="e1 = 3">
            Back
          </v-btn>
          <v-btn @click="install" color="primary">
            Install
          </v-btn>
        </v-stepper-content>
        <v-stepper-content step="5">
          <div style="text-align: left">
            Start Service
          </div>
          <v-btn @click="e1 = 4">
            Back
          </v-btn>
          <v-btn @click="callService('start')" color="primary">
            Start
          </v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
    <v-overlay :value="overlay">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
  </div>
</template>

<script>
import axios from "../axios";
import {serviceItems} from "../constants/appConstant";

export default {
  data() {
    return {
      e1: 1,
      releases: [],
      serviceStatus: {},
      selectedId: '',
      selectedRelease: {author: {}},
      overlay: false,
      service: {}
    };
  },
  mounted() {
    const { id } = this.$route.params;
    this.getService(id);
  },
  computed: {
    installedService() {
      const { installedServices } = this.$store.state;
      const serviceItem = serviceItems.find(s => s.id === this.service.name);
      return installedServices.find(s => s.service === serviceItem.fileName);
    }
  },
  methods: {
    async getService(id) {
      const service = await axios.get(`service/${id}`);
      this.service = service;
      this.getReleaseList();
      this.getServiceStatus();
    },
    async install() {
      this.$store.commit('changeLoading', true);
      const data = { service: this.service.name, version:this.selectedRelease.tag_name};
      axios.post(`rubix-service/install-service`, data).then(result => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show('Install completed')
        this.e1 = 5;
      })

    },
    async download() {
      this.$store.commit('changeLoading', true);
      const data = { service: this.service.name, version:this.selectedRelease.tag_name};
      axios.post(`rubix-service/download-service`, data).then(r => {
        this.$toasted.show('Download completed');
        this.$store.commit('changeLoading', false);
        this.e1 = 4;
      })
    },
    async getServiceStatus() {
      this.$store.commit('changeLoading', true);
      axios.get(`rubix-service/up-service/${this.service.name}`).then(result => {
        this.$store.commit('changeLoading', false);
        this.serviceStatus = result;
      })

    },
    async stopService() {
      if (!this.serviceStatus.status) {
        this.e1 = 3;
        return;
      }
      await this.callService('stop');
      this.e1 = 3;
    },
    async callService(action) {
      this.$store.commit('changeLoading', true);
      axios.post(`rubix-service/call-service`, {action, service: this.service.name}).then(result => {
        this.$store.commit('changeLoading', false);
        this.$toasted.show(`${action} success!`);
      })
    },
    getReleaseList() {
      this.$http.get(this.service.releaseUrl).then(({data}) => {
        let releases = data;
        if (this.service.fromVersion) {
          releases = data.filter(x => x.tag_name >= this.service.fromVersion);
        }
        this.releases = releases;
        this.selectedId = this.releases[0].id;
        this.selectedRelease = this.releases[0];
      });
    }
  },
  watch: {
    selectedId: function(val, oldVal) {
      this.selectedRelease = this.releases.find(r => r.id === val);
    },
    '$route.params.id': function(val, oldVal) {
      this.getService(val);
    },
  }
};
</script>
