<template>
  <v-card style="min-height: calc(100vh - 24px)">
    <div class="text-left" style="padding:20px">
      <v-btn x-small color="primary" @click="clickImport" >
        Import&nbsp;<v-icon small>mdi-import</v-icon>
      </v-btn>
      <v-divider inset vertical></v-divider>
      <v-btn x-small @click="clickJsonExport" color="#009688">
        Export&nbsp;all &nbsp;<v-icon small>mdi-export</v-icon>
      </v-btn>
      <v-divider inset vertical></v-divider>
      <v-btn x-small @click="showVariableDialog" color="success">
        Variables&nbsp;<v-icon small>mdi-variable</v-icon>
      </v-btn>
      <v-btn x-small @click="sendAll" color="warning">
        Automation&nbsp;<v-icon small>mdi-home-automation</v-icon>
      </v-btn>
      <v-btn x-small @click="saveAll" color="info">
        Save All
      </v-btn>
    </div>
    <v-card-text>
      <span style="display: none">{{items[1] ? items[1].name: ''}}</span>
      <v-tabs v-model="tab">
        <draggable v-model="items" @start="drag=true" @end="onEnd">
          <v-tab class="api-tab" :key="index" v-for="(item, index) of items">
            {{item.name}} &nbsp;
            <v-switch @change="() => saveData(item)" v-model="item.enable"></v-switch>
            <v-btn @click.native="removeItem(index)" class="btn-delete" icon x-small color="warning">
              <v-icon x-small>mdi-close-circle</v-icon>
            </v-btn>
          </v-tab>
          <v-tab class="api-tab" @click.native="addItem" :key="items.length">
            <v-icon>mdi-plus</v-icon>
          </v-tab>
        </draggable>

        
    </v-tabs>
    <v-tabs-items v-model="tab" >
      <v-tab-item :key="index" v-for="(item, index) of items">
        <v-card flat>
          <v-card-text>
            <api-client-tab
              :send="(item) => sendRequest(item, index)"
              :save="onClickSave"
              :resp="resps[index]"
              :item="item" >
            </api-client-tab>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
    </v-card-text>
    <v-dialog v-model="importDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Import</span>
          </v-card-title>

          <v-card-text>
            <v-alert
              class="text-left"
              dense
              colored-border
              type="error"
              elevation="2"
              v-show="disableSave && dataImport"
            >Incorrect Data Type!</v-alert>
            <v-container>
              <v-textarea
                outlined
                name="input-7-4"
                label="import data"
                v-model="dataImport"
                @input="onChangeImportData"
              ></v-textarea>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
            <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
      <v-dialog v-model="variablesDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">Global Variables</span>
          </v-card-title>

          <v-card-text>
            <key-value-form :items="variables"></key-value-form>
          </v-card-text>saveAll
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="closeVariableDialog">Cancel</v-btn>
            <v-btn color="blue darken-1" text v-on:click="saveVariables">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
  </v-card>

</template>

<script>
import ApiClientTab from './ApiClientTab';
import KeyValueForm from './KeyValueForm.vue';
import draggable from 'vuedraggable'
import { copyToClipboard, isJsonString } from '../utils/utils';
import axios from '../axios';
import _axios from 'axios';

export default {
    components: {
        ApiClientTab,
        KeyValueForm,
        draggable
    },
    data: () => ({
        variablesDialog: false,
        importDialog: false,
        disableSave: true,
        variables: [],
        dataImport:"",
        tab: 0,
        resps: [],
        items: [{}],
        defaultItems: [{name: 'Test', method: 'Get', params: [], headers: [], body: []}]
    }),
    mounted() {
        this.getData();
        const { setting } = this.$store.state;
        this.variables = setting.variables || [];
    },
    methods: {
        async onEnd(){
          this.drag = false;
          let index = 0;
          for (const item of this.items) {
            item.index = index;
            await this.saveData(item);
            index++;
          }
        },
        async saveAll() {
          for (const item of this.items) {
            await this.saveData(item);
          }
          this.getData();
        },
        async sendAll() {
          let index = 0;
          this.$store.commit('changeLoading', true);
          for (const item of this.items) {
            if (!item.enable || !item.url) {
              continue;
            }
            try {
              const { data } = await this.send(item, index);
              this.resps[index] = data;
              this.resps = [...this.resps];
              this.$toasted.show(`${item.name} success!`);
            } catch(err) {
              const { response } = err;
              if (response && response.data) {
                this.resps[index] = response.data;
              } else {
                this.resps[index] = {error: `Can not call to ${item.url}`}
              }
              this.$toasted.show(`${item.name} failed!`, {type: 'error'});
            }
            index++;
          }
          this.$store.commit('changeLoading', false);
        },
        sendRequest(item, index) {
          this.$store.commit('changeLoading', true);
          this.send(item, index).then(({data}) => {
            this.resps[index] = data;
            this.resps = [...this.resps];
            this.$store.commit('changeLoading', false);
          }).catch((err) => {
            const { response } = err;
            this.$store.commit('changeLoading', false);
            if (response && response.data) {
              this.resps[index] = response.data;
            } else {
              this.resps[index] = {error: `Can't call to ${item.url}`}
            }
            this.resps = [...this.resps];
          })
        },
        send(item, index) {
          const { variables } = this.$store.state.setting;

          if (variables) {
              for (const variable of variables) {
                  if (variable.key && variable.value && variable.enable) {
                      window[variable.key] = variable.value;
                  }
              }
          }

          const headers = {};
          for (const header of item.headers) {
            if (header.key && header.value && header.enable) {
              headers[eval('`' + header.key + '`')] = eval('`' + header.value + '`');
            }
          }

          const body = {};
          for (const bodyItem of item.body) {
            if (bodyItem.key && bodyItem.value && bodyItem.enable) {
              body[eval('`' + bodyItem.key + '`')] = eval('`' + bodyItem.value + '`');
            }
          }
          const url = eval('`' + item.url + '`');
          const method = item.method.toLocaleLowerCase();

          return (method === 'get' || method === 'delete')
            ? _axios[method](url, {headers})
            : _axios[method](url, body, {headers});
        },
        async onClickSave(item) {
          const r = await this.saveData(item);
          await this.getData();
          this.$toasted.show(r.status);
        },
        saveVariables() {
          const { setting } = this.$store.state;
          setting.variables = this.variables;

          this.$store.commit('changeLoading', true);
          axios.put(`setting`, setting).then(result => {
            this.$store.commit('changeLoading', false);
            if (result === 'successful') {
              this.$store.commit('changeSetting', setting);
              this.$toasted.show('Success!');
              this.variablesDialog = false;
            }
          });
        },
        closeVariableDialog() {
          this.variablesDialog = false;
        },
        showVariableDialog() {
          this.variablesDialog = true;
        },
        async onChangeImportData(){
          this.disableSave = !isJsonString(this.dataImport) || !Array.isArray(JSON.parse(this.dataImport));
        },
        clickJsonExport(){
          const status = copyToClipboard(JSON.stringify(this.items));
          const msg = status ? `Clipboard Copy Successful!` : `Failure on copying on clipboard!`;
          const type = status ? `success` : `error`;
          return this.$toasted.show(msg, {type: type});
        },
        clickImport() {
          this.importDialog = true;
        },
        async saveImport() {
          const items = JSON.parse(this.dataImport);
          for (const item of items) {
            await this.saveData(item);
          }
          this.importDialog = false;
          this.getData();
        },
        closeDialogImport(){
          this.importDialog = false;
        },
        async removeItem(index) {
            const item = this.items[index];
            if (item._id) {
                await this.deleteData(item._id);
            }
            this.items.splice(index,  1);
        },
        async deleteData(id) {
          const r = await axios.delete(`api-client/${id}`)
          this.$toasted.show(r.status);
        },
        async saveData(item) {
            if (!item.headers) {
              item.headers = [];
            }
            if (!item.body) {
              item.body = [];
            }
            if (!item.params) {
              item.params = [];
            }
            const r = await axios.put('api-client', item)
            return r;
        },
        addItem() {
          const item = JSON.parse(JSON.stringify(this.defaultItems[0]))
          this.items.push(item);
          this.tab = this.items.length - 1;
        },
        async getData() {
          const r = await axios.get('api-client');
          this.items = r.length === 0 ? [...this.defaultItems]: r;
        }
    }
};
</script>
<style>
  .btn-delete {
    position: absolute;
    right: 2px;
    top: 2px;
  }
  .api-tab {
    float: left;
    height: 100%;
    min-width: 200px;
  }
</style>
