<template>
  <div>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0">
        Services
      </v-tab>
      <v-tab :key="1">
        Installed Services
      </v-tab>
      <v-tab :key="2" v-if="setting.networking">
        Networking
      </v-tab>
      <v-tab :key="3">
        Host Info
      </v-tab>
      <v-tab :key="4">
        Device
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <service-items></service-items>
      </v-tab-item>
      <v-tab-item :key="1">
        <installed-services></installed-services>
      </v-tab-item>
      <v-tab-item :key="2" v-if="setting.networking">
        <networking></networking>
      </v-tab-item>
      <v-tab-item :key="3">
        <host-info></host-info>
      </v-tab-item>
      <v-tab-item :key="4">
        <device-detail-form></device-detail-form>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import ServiceItems from './ServiceItems';
import InstalledServices from './InstalledServices';
import Networking from './Networking';
import HostInfo from './HostInfo';
import DeviceDetailForm from './DeviceDetailForm';

export default {
  name: "services",
  components: {
    ServiceItems,
    Networking,
    InstalledServices,
    DeviceDetailForm,
    HostInfo,
  },
  data:()=>({
    tab: 0,
    tabs: ['items', 'installed-services', 'networking', 'host-info', 'device-detail'],
    isDisabled:false
  }),
  mounted() {
    this.setTab();
  },
  watch: {
    '$route.query.tab':function(val){
      this.tab = this.tabs.indexOf(val);
    },
    tab: function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'services', query: { tab: tabKey } });
      }
    },
  },
  mounted(){
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
  methods: {
    setTab() {
      const { tab: tabKey } = this.$route.query;
      const { setting } = this.$store.state;
      if (setting.networking) {
        this.tabs.splice(2, 0, 'networking');
      }
      if (tabKey === 'networking' && !setting.networking) {
        this.$router.replace(`/exception-403`);
      }
      this.tab = tabKey ? this.tabs.indexOf(tabKey): 0;
    }
  },
  computed: {
    setting () {
      return this.$store.state.setting;
    }
  },
};
</script>

