<template>
  <div>
    <v-card elevation="2">
      <v-card-text>
        <v-row>
          <v-spacer></v-spacer>
          <div class="text-right mb-5">
            <v-btn text small color="warning" @click="messages = []">Clear</v-btn>
          </div>
        </v-row>
        <div id="message-container">
          <div v-for="(m, index) in messages" :key="index" class="message">
            <v-chip
              :class="`${m.type}-message border-left`"
              :style="`border-color: ${m.color}; border-radius: 5px;`"
            >
              Topic: {{m.topic}} <br /> Message: {{m.text}}
            </v-chip>
          </div>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import axios from "../axios";

export default {
  data:()=>({
    messages: [],
  }),
  mounted() {
    this.socket = new WebSocket('ws://localhost:8081');
    this.socket.addEventListener('message', ({data}) => {
      const message = JSON.parse(data);
      if (message.topic === 'rubix/lora_raw/debug') {
        this.messages.push({...message, type: 'receive'});
      }
    });
    this.subscribeTopic()
  },
  methods: {
    subscribeTopic() {
      axios.get('mqtt/subscribe/rubix/lora_raw/debug').then(r => {
        this.$toasted.show(r.status);
      })
    }
  },
  
};
</script>

<style>
#message-container {
  height: 70vh;
  overflow: auto;
}
.message {
  width: 100%;
  display: block;
  overflow: auto;
  margin-bottom: 10px;
}
.message .v-size--default {
  height: 48px;
}
.receive-message {
  display: block;
  float: left;
}
.message .v-chip__content {
  text-align: left;
}
.border-left {
  border-left: 1px solid;
  border-width: 4px!important;
}
.active {
  background-color: #666666 !important;
}
</style>
