<template>
  <v-app>
    <Sidebar :drawer="drawer"/>
    <v-main>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-main>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card color="primary" dark>
        <v-card-text>
          Loading
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
  import Sidebar from "./Sidebar";
  import logo from '../assets/nube-frog.png';
  import axios from "../axios";
  export default {
    components: {Sidebar},
    data() {
      return {
        user: "",
        logo: logo,
        drawer: true,
      }
    },
    computed: {
      loading () {
        return this.$store.state.loading;
      }
    },
    methods:{
      getPingData(){
        axios.get('point-server/ping')
        .then(res=>{
          this.$store.commit('changePingData',res);
        });
      },
      getIframes() {
        axios.get('iframe').then(r => {
          this.$store.commit('changeIframes', r);
        })
      },
      getInstalledServices() {
        axios.get('rubix-service/installed-services').then(r => {
          this.$store.commit('changeInstalledServices', r);
        })
      },
      getMqttConfig() {
        axios.get('mqtt-config').then(r => {
          this.$store.commit('changeMqttConfig', r);
          // this.mqttConfig.subscriptions.forEach(s=>s.enable = true);
        })
      },
      getServices() {
        axios.get('service').then(r => {
          this.$store.commit('changeServices', r);
        })
      },
    },
    created() {
      window.EventBus.$on('show-toast', ($event) => {
        this.$toasted.show($event,{type: "error"});
      });
      window.EventBus.$on('change-loading', ($event) => {
        this.$store.commit('changeLoading', $event);
      });
    },
    mounted(){
      this.getIframes();
      this.getServices();
      this.getInstalledServices();
      this.getMqttConfig();
    }
  }
</script>
<style>
  .v-toolbar {
    flex: none;
  }
  .v-application--wrap {
    display: inline-block !important;
  }
</style>
